import React, { ReactElement } from "react";

export default function Objective (): ReactElement {
  return (
    <div className={`rounded-md w-full my-2 bg-[whitesmoke] text-dark-blue text-lg p-2`}>
      <p className={`mb-1`}>
        Motivated community-taught software developer seeking to use proven skills in JavaScript to deliver high quality, working, and maintainable solutions to UI/UX challenges.
      </p>
      <p>
        Passionate about building first-class responsive web applications. Experience includes coding, troubleshooting, and testing.
      </p>
    </div>
  );
}